* {
    font-family: 'Kelly Slab';
    /* font-family: 'Ruslan Display'; */
}

body {
    margin: 0;
    background-image: linear-gradient(to right, rgb(190, 27, 27),rgb(240, 42, 42) , rgb(150, 25, 25));
    display: flex;
    flex-direction: column;
}
header {
    height: 40px;
    background-color: rgb(71, 71, 71); 
    width: 100%;
}


.logo0 {
    align-items: center;
    display: flex;
    flex-direction: row;
    gap: 10px;
}
.logo {
    background-image: url(logo/tire-svgrepo-com.svg);
    background-size: contain;
    background-repeat: no-repeat;
    width: 30px;
    height: 30px;
} 
nav {
    height: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    gap: 30px;
    align-items: center;
    padding: 0 30px;
    font-family: inherit;
    text-decoration: none;
    color: rgb(255, 255, 255);
}
ul {
    margin: 0;
} 
ul li {
    min-width: 24px;
}
a {
    color: inherit;
    text-decoration: none;
}

header a:active {
    color: crimson;
}

.menu {
    list-style-type: none;
    display: flex;
    flex-direction: row;
    gap: 20px;
    /* font-family: Andale Mono, monospace; */
    font-weight: bold;
    font-size: 15px;
}


.mainImg {
    background-image: url(cars/red-sports-car-ferrari-png-23.png);
    background-size: contain;
    background-repeat: no-repeat;
    background-position-x: center;
    width: 90%;
    height: 500px;
    /* background-color: rgb(25, 26, 36); */
    margin-left: auto;
    margin-right: auto;
    display: flex;
    align-items: center;
    justify-content: center;
}
.mainImg:hover {
    transform: scale(1.1);
    transition: transform 6s cubic-bezier(0.25, 0.45, 0.45, 0.95);
}

.company {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    /* background-color: violet; */
}


.MI-text1 {
    color: rgb(255, 255, 255);
    font-size: 50px;
    margin: 0;
    /* font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif; */
    font-weight: bold;
    letter-spacing: 3px;
    text-shadow: 2px 2px rgb(82, 82, 82);
}
.MI-text1-2 {
    color: rgb(82, 82, 82);
    text-shadow: 2px 2px rgb(255, 255, 255);
}
.MI-text2 {
    width: 80%;
    height: 50px;
    border: 0;
    border-radius: 10px;
    background-color: rgba(255, 217, 0, 0.9);
    color: rgb(0, 0, 0);
    text-shadow: rgb(250, 250, 5, 0.3) 1px 1px; 
    /* font-family: Andale Mono, monospace; */
    font-weight: bold;
    font-size: x-large;
    display: flex;
    justify-content: center;
    align-items: center;
}
/* .MI-text2:active {
    background-color: rgb(250, 58, 24);
} */
.MI-text2:hover {
    background-color: rgb(0, 0, 0, 0.9);
    color: rgb(170, 22, 22);
}
.MI-text2:active {
    background-color: rgb(170, 22, 22);
    color: wheat;
}

.secPict0 {
    background-color: rgb(71, 71, 71);
    color: white;
    display: flex;
    justify-content: center;
}
.secPict0-p {
    font-size: x-large;
    /* font-family: Andale Mono, monospace; */
    font-family: 'Kelly Slab', cursive;
    /* font-family: 'Ruslan Display', cursive; */
    font-weight: bold;
    margin-left: 5%;
}


.secPict {
    width: 100%;
    height: auto;
    background-color: rgb(255, 255, 255);
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    flex-direction: row;
    align-items: center;
    padding: 2% 0;
    margin-left: auto;
    margin-right: auto;
}
/* .secPict div {
    font-family: 'Gill Sans', 'Gill Sans MT', 'Trebuchet MS', serif;
    font-weight: 700;
    color: white;
} */
.secPict-0 {
    width: calc(33% - 42px);
    height: 280px;
    background-repeat: no-repeat;
    background-color: rgb(71, 71, 71);
    border-radius: 10px;
    margin: 7px;
    padding: 5px 0; 

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.secPict-0:hover {
    transform: scale(1.05);
    transition: transform 3s cubic-bezier(0.25, 0.45, 0.45, 0.95);
    background-color: rgb(34, 34, 34);
}

.part1 {
    /* background-color: white;
    border-radius: 15px;
    width: 90%;
    height: 90%;
    display: flex;
    justify-content: space-evenly;
    align-items: center; */
    width: 90%;
    height: 90%;
    border-radius: 3px;
    background-repeat: no-repeat;
    background-position-x: center;
    background-position-y: center;
    background-size: cover;
}
.part2 {
    color: white;
    font-size: large;
    width: 90%;
    height: 10%;
    display: flex;
    justify-content: center;
    align-items: center;
}

