footer {
    margin-top: 100px;
    display: flex;
    height: 70px;
    justify-content: space-around;
    flex-direction: row;
    align-items: center;
    background-color: rgb(71,71,71);
    color: rgb(255, 255, 255);
    font-family: Andale Mono, monospace;
    font-weight: bold;
}
.footer div {
    display: flex;
    flex-direction: column;
}

.foot-num {
    max-height: 50px;
}
.foot-em {
    max-height: 50px;

}

footer p:last-child:hover {
    color:rgb(240, 42, 42)
}

.foot-num-1 {
    font-size: 15px;
    margin: 0;
}
.foot-em-1 {
    font-size: 15px;
    margin: 0;
}
.foot-num-2 {
    font-size: 20px;
    margin: 0;
}
.foot-em-2 {
    font-size: 20px;
    margin: 0;
}

@media screen and (min-width: 539px) and (max-width: 992px) {
    .mainImg {
        height: 250px;
        margin-top: 32px;
    }
    .secPict0-p {
        font-size: 25px;
    }
    .secPict0 {
        margin-top: 50px;
    }
    .secPict {
        margin-bottom: 10px;
        display: flex;
        justify-content: space-around;
    }
    .secPict-0 {
        width: 300px;
        height: 220px;
        padding: 0;
    }
    .MI-text1 {
        font-size: 30px;
    }
    .MI-text2 {
        font-size: 10px;
    }
    footer {
        margin-top: 150px;
    }

    .foot-num-1 {
        font-size: 13px;
    }
    .foot-em-1 {
        font-size: 13px;
    }
    .foot-num-2 {
        font-size: 17px;
    }
    .foot-em-2 {
        font-size: 17px;
    }
    .part2 {
        font-size: 20px;
    }
}


@media screen and (width: 540px) {
    .secPict0 {
        margin-top: 0;
    }
    .secPict {
        margin-bottom: 10px;
        display: flex;
        justify-content: space-around;
    }
    .secPict-0 {
        width: 200px;
        height: 160px;
        padding: 0;
    }
    footer {
        margin-top: 20px;
        font-size: 12px;
    }
    .foot-num-1 {
        font-size: 10px;
    }
    .foot-em-1 {
        font-size: 10px;
    }
    .foot-num-2 {
        font-size: 13px;
    }
    .foot-em-2 {
        font-size: 13px;
    }
    .part2 {
        font-size: 15px;
    }
}
@media screen and (width: 912px) {
    .mainImg {
        margin-top: 50px;
    }
    .secPict0 {
        margin-top: 50px;
    }
    .secPict-0 {
        width: 400px;
        height: 280px;
        margin-top: 15px;
        margin-bottom: 15px;
    }
    .MI-text1 {
        font-size: 30px;
    }
    .MI-text2 {
        font-size: 10px;
    }
    footer {
        margin-top: 170px;
    }
}


@media screen and (max-width: 538px) {
    * {
        font-size: 10px;
    }
    .secPict0-p {
        font-size: 10px;
    }
    .mainImg {
        height: 150px;
    }
    .MI-text1 {
        font-size: 20px;
    }
    .MI-text2 {
        height: 30px;
    }
    .secPict {
        display: flex;
        justify-content: center;
    }
    .secPict-0 {
        width: 65%;
        height: 150px;
        /* padding: 0; */
        padding-top: 5px;
    }

    header div, a {
        font-size: 8px;
    }

    footer {
        margin-top: 20px;
    }
    .foot-num-1 {
        font-size: 8px;
    } 
    .foot-em-1 {
        font-size: 8px;
    }
    .foot-num-2 {
        font-size: 12px;
    }
    .foot-em-2 {
        font-size: 12px;
    }
    .part2 {
        font-size: 10px;
    }
}
@media screen and (width: 280px) {
    .secPict0 {
        margin-top: -20px;
    }
    .secPict-0 {
        height: 130px;
    }
    .company {
        margin-bottom: 25px;
    }
    .MI-text2 {
        font-size: 10px;
    }
    .foot-num-1 {
        font-size: 8px;
    }
    .foot-em-1 {
        font-size: 8px;
    }
    .foot-num-2 {
        font-size: 10px;
    }
    .foot-em-2 {
        font-size: 10px;
    }
}